@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  min-height: 100%;
  line-height: 1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}

input,
select,
option,
optgroup,
textarea,
button,
pre,
code {
  font-size: 100%;
  font-family: inherit;
}

ol,
ul {
  list-style: none;
}

/*thank you Erica for asking the question in OH*/
html {
  scroll-behavior: smooth;
}

/*set color and font variables*/
:root {
  --light-turq: #33c5ff;
  --dark-grey: #363642;
  --dark-blue: #094d9b;
  --large-pad: 50px;
  --small-pad: 10px;
  --mini-pad: 5px;
  --micro-pad: 2px;
}

body {
  font-family: "Syne Mono", monospace;
  /* background-image: url("./images/blue_herring.jpeg");
  background-size: cover; */
  background-image: url(/portfolio/static/media/bg.b3d1af99.jpg);
  background-size: cover;
}

.nav .current {
color: green;
 border: none;
 border-bottom: green solid 2px;
 border-bottom: green solid var(--micro-pad);
}

p {
  font-size: 125%;
}
/*top headings*/
.h1 {
  background-color: none;
  color: green;
}
.h2 {
  color: #094d9b;
  color: var(--dark-blue);
}
.subheader {
  background: transparent;
}

.work-header {
  color: #094d9b;
  color: var(--dark-blue);
}

.nav {
  border-bottom: green solid 2px;
  border-bottom: green solid var(--micro-pad);
}


.nav li {
  display: inline;
}

.nav button {
  padding-bottom: 2px;
  padding-bottom: var(--micro-pad);
  color: #094d9b;
  color: var(--dark-blue);
  text-decoration: none;
  border: none;
  /* border-bottom: var(--dark-blue) solid var(--micro-pad); */
  background: none;
}

.nav button:hover {
 color: green;
 border: none;
 cursor: pointer;
 border-bottom: green solid 2px;
 border-bottom: green solid var(--micro-pad);
}

.contact-header {
  color: #33c5ff;
  color: var(--light-turq);
}

.about {
  color:#094d9b;
  color:var(--dark-blue);
  text-align: left;
  background-color: transparent;
  border-radius: 10px;
  padding: 5px;
  margin-top: 25px;
}

.resume {
  color:#094d9b;
  color:var(--dark-blue);
  text-align: left;
  background-color: transparent;
  border-radius: 10px;
  padding: 20px;
}

.bio_pic {
  width: 25%;
}

.bio {
  display: flex;
  padding: 10px;
}


/*sample cards with transparent effect*/
.card {
  background-size: 100%;
  filter: none;
  position: relative;
  border-radius: 10px;
  cursor: pointer;
}

.card:hover {
  filter: none;
}

/*style titles inside cards*/
.h4 {
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 10px;
  padding: 5px;
  padding: var(--mini-pad);
  text-align: left;
  position: absolute;
  top: 5%;
  left: 50px;
  left: var(--large-pad);
  color: black;
}

.h4 h3 {
  font-size: 150%;
}

/* individual backgrounds for cards */
.surf {
  background-image: url(/portfolio/static/media/surf.0489a528.jpeg);
  background-color: skyblue;
  filter: opacity(0.75);
}

.LED {
  background-image: url(/portfolio/static/media/planner.f392142e.jpeg);
  background-color: magenta;
  filter: opacity(0.75);
}

.calc {
  background-image: url(/portfolio/static/media/key.44c510c0.jpeg);
  background-color: grey;
  filter: opacity(0.75);
}

.eight_ball {
  background-image: url(/portfolio/static/media/eight_ball.650b17ef.png);
  background-color: white;
  background-position: top;
  filter: opacity(0.75);
  background-size: 25%;
}

.run {
  background-image: url(/portfolio/static/media/run.39a8bcd9.jpeg);
  background-color: green;
  filter: opacity(0.75);
}

ol li {
  padding: 10px;
  font-size: 125%;
  list-style: "⚙️ "; 
}

.info {
  display: flex;
  flex-wrap: wrap;
}

.info-item {
  margin: 10px;
  margin: var(--small-pad);
}

.info-item a {
  padding-bottom: 2px;
  padding-bottom: var(--micro-pad);
  color: #094d9b;
  color: var(--dark-blue);
  text-decoration: none;
  border: none;
  background: none;
}

.info-item a:hover {
 color: green;
 border: none;
 cursor: pointer;
 border-bottom: green solid 2px;
 border-bottom: green solid var(--micro-pad);
}

#download {
  margin: 25px;
  margin-top: 25px;
}

#download a {
  padding-bottom: 2px;
  padding-bottom: var(--micro-pad);
  color: #094d9b;
  color: var(--dark-blue);
  text-decoration: none;
  border: none;
  background: none;
}

#download a:hover {
  color: green;
 border: none;
 cursor: pointer;
 border-bottom: green solid 2px;
 border-bottom: green solid var(--micro-pad);
}

input {
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  border: none;
}

textarea {
  height: 250px;
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  flex: 100% 1;
  border: none;
  margin: 10px;
}

.submit {
  color: #094d9b;
  color: var(--dark-blue);
  text-decoration: none;
  border: none;
  border-bottom: #094d9b solid;
  border-bottom: var(--dark-blue) solid;
  background: none;
  font-size: 125%;
  margin: 30px;
}


@media screen and (min-width: 175px) {
  
  body {
    flex-direction: column;
  }
  .space-fill-1 {
    height: 50px;
  }

  .form {
    display: block;
    justify-content: center;
    padding: 20px;
    margin-right: 25px;
  }
  
  h1 {
    text-align: left;
    flex: 100% 1;
    font-size: 200%;
  }

  .h2 {
    font-size: 175%;
    padding: 10px;
    padding: var(--small-pad);
  }

  .h2 h2 {
    text-align: center;
  }

a {
  text-decoration: none;
  /* color: black; */
}
  .h3 {
    padding: 5px;
    padding: var(--mini-pad);
    margin: 5px 5px 5px 0;
    margin: var(--mini-pad) var(--mini-pad) var(--mini-pad) 0;
    color: green;
    font-size: 200%;
    margin-left: 5%;
  }

  .nav {
    display: flex;
    justify-content: space-between;
    line-height: 200%;

  }

  .nav li {
    font-size: 125%;
    padding: 10px;
    padding: var(--small-pad);
  }

  #resume {
    display: block;
    margin: 25px;
  }

  .res_title {
    margin-bottom: 20px;
  }

  #gear {
    font-size: 150px;
    text-align: center;
  margin-top: 25px;
  }

  .bio_pic {
    width: 200px;
    margin-left: 50px;
    margin-bottom: 25px;
  }
  
  .bio {
    display: block;
    padding: 10px;
  }

  .info {
    display: block;
    padding-left: 20%;
  }

  .info-item {
    padding-block: 10px;
  }

  .samples {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 10px;
    padding: var(--small-pad);
  }
  .card {
    height: 150px;
    margin: 5px;
    margin: var(--mini-pad);
  }

  .subheader {
  
    padding-top: 50px;
  
    padding-top: var(--large-pad);
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    margin-left: 3%;
    margin-right: 4%;
    padding: 10px;
  }
}

@media screen and (min-width: 768px) {
  .space-fill-2 {
    height: 100px;
  }

  .form {
    display: block;
    margin-left: 20%;
    margin-right: 20%;
    padding: 20px;
    margin-top: 25px;
  }

  h1 {
    text-align: left;
    font-size: 200%;
  }

  .h2 {
    font-size: 175%;
  }
.h2 h2 {
  text-align: left;
}
  .nav {
    display: flex;
    justify-content: space-between;
    line-height: 200%;
  }

  .nav li {
    font-size: 125%;
    padding: 10px;
    padding: var(--small-pad);
  }
  
  .subheader {
    display: block;
  }

  .bio {
    display: flex;
    flex-wrap: flex;
    align-items: center;
  }

  .bio_pic {
    width: 200px;
    margin: 20px;
  }

  #resume {
    display: flex;
    align-items: top;
  }

  .info {
    display: flex;
    flex-wrap: wrap;
    margin-left: -20%;
  }

  .info-item {
    margin: 10px;
    margin: var(--small-pad);
    font-size: 150%;
  }
  /* ensures samples cards have space gap */
  .samples {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    grid-gap: 5px;
    gap: 5px;
    grid-gap: var(--mini-pad);
    gap: var(--mini-pad);
  }

  .card:first-child {
    flex: 0 0 98%;
    height: 250px;
  }

  .card {
    flex: 0 0 48%;
    height: 125px;
  }

  .subheader {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
  }
}

/* media query for larger screens */
@media screen and (min-width: 992px) {
  .about {
  margin-left: 18.5%;
  margin-right: 20%;
  }

  #resume {
    display: flex;
    margin-left: 18.5%;
    margin-right: 22%;
    align-items: top;
  }

  .space-fill-1 {
    height: 100px;
  }
  .info {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .info-item {
    padding: 20px;
  }
  .samples {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 5px;
    gap: 5px;
    grid-gap: var(--mini-pad);
    gap: var(--mini-pad);
    width: 65%;
    margin: 0 auto;
  }
  .card:first-child {
    flex: 0 0 97%;
    height: 300px;
  }

  .card {
    flex: 0 0 48%;
    height: 150px;
  }

  .h1 {
    display: flex;
    font-size: 200%;
    flex: wrap 1;
  }

  .h2 {
    font-size: 175%;
  }

  .h2 h2 {
    margin-left: 20%;

  }

  a {
    text-decoration: none;
  }

  .nav li {
    font-size: 150%;
    padding: 5px;
    padding: var(--mini-pad);
    margin: 5px;
    margin: var(--mini-pad);
  }

  .nav {
    display: flex;
    flex: wrap 1;
    justify-content: space-between;
    margin-left: 22%;
  }

  .subheader {
    display: flex;
    flex-wrap: wrap;
    margin-left: 18.5%;
    margin-right: 20%;
    padding: 10px;
    justify-content: space-between;
  }
}

